body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,h2,h3,h4 {
  margin: 0
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
 
}
.initButtom {
    padding: 20px;
    margin-bottom: 25px;
    background-color: red;
}
.homePage {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column; }

.form1 {
    display: flex;
    flex-direction: column;
}

.form1 input {
    border:solid 1px black;
}
.form2 {
    display: flex;
    flex-direction: column;
}

.form2 input {
    border:solid 1px black;
}
.form3 {
    display: flex;
    flex-direction: column;
}

.form3 input {
    border:solid 1px black;
}

.loading {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .loading img {
    width: 10%; }

.navBar {
  background-color: transparent;
  position: fixed;
  top: 0;
  background-color: white;
  z-index: 99;
  width: 100%;
  height: 8vh;
  box-shadow: 0px -5px 19px 2px grey;
  display: flex;
  align-items: center;
  justify-content: center; }
  .navBar nav {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    font-weight: bold;
    font-size: 1.3em; }
  .navBar .pink {
    color: #f0225b; }

.secondNav {
  display: flex;
  align-items: center;
  justify-content: center; }

.imgContainer {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  overflow: hidden;
  margin-right: 10px; }

.imgContainer img {
  max-width: 100%;
  object-fit: contain; }

.profileContainer {

    position: relative;
    top:8vh;
}

.description,.interestsInformation,.photo {
    border:solid 1px grey;
    margin-bottom: 2%;
    padding: 2% 0;
}


.photo img{
    border-radius: 50%;
    max-width: 20%;
    border:solid 1px grey;
}

.photo img{
    border-radius: 50%;
    max-width: 20%;
    border:solid 1px grey;
}

textarea {
    width: 95%;
    height: 60px;
}

.birth input {
    width: 30%
}


.interestActive {
    background-color: rgba(134, 255, 215, 0.671);
}
.chatBox {
  height: 19vh;
  margin-bottom: 3%;
  display: flex;
  align-items: center;
  position: relative;
  top: 8vh;
  padding: 0 8%; }

.chatBox:last-child {
  margin-bottom: 10vh !important; }

.chatBox .chat {
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80%; }

.imgContainer {
  width: 60px;
  height: 60px;
  margin-right: 8%; }

.imgContainer img {
  max-width: 100%; }

.userDescription {
  overflow-y: -webkit-paged-y;
  height: 8vh; }

.imgNoChat {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }
  .imgNoChat img {
    max-width: 100%; }

.placesContainer {
    position: relative;
    top:8vh;
}
.mainUser {
  background-color: rgba(110, 110, 255, 0.623);
  margin-left: 80%;
  width: 60px;
  margin-bottom: 3%; }

.invitedUser {
  background-color: rgba(0, 128, 0, 0.562);
  margin-left: 2%;
  width: 60px;
  margin-bottom: 3%; }

.buddiesContainer {

    position: relative;
    top:9vh;
}

.navBuddy {

    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top:3%;
}

.buddiesImg {
    max-width: 20%;
    border-radius: 50%;
}

.buddyPanel {
    padding: 0 4%;
}

.none {
    display: none;
}

.buddyHidden {
    display: none;
}

.buddyCircle{

    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: rgb(97,168,169);
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.buddyCircle.lead{
    background-color: rgb(194 200 200);

}
.buddyCircle p {
    
    text-align: center;
    color:white;
    font-weight: bold;
}

.underlineBuddy {
    width: 100%;
    height:4px;
}

.underlineBuddy .first{

    background-color: rgb(97,168,169);
    width: 50%;
    margin-top: 2%;
    margin-bottom: 2%;
    height:4px;
}   
.hiddenTabBar {
  display: none; }

.showTabBar {
  display: block; }

.tabBar {
  background-color: white;
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  top: 90vh;
  box-shadow: 0px 5px 14px 2px grey; }

.tabBar a {
  color: #0b2323;
  text-decoration: none; }

.tabBar .icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.showTabBar img.show {
  display: none; }

.showTabBar.chat .chat img.hidden,
.showTabBar.buddies .buddies img.hidden,
.showTabBar.place .places img.hidden,
.showTabBar.profile .profile img.hidden {
  display: none; }

.showTabBar.chat .chat img.show,
.showTabBar.buddies .buddies img.show,
.showTabBar.place .places img.show,
.showTabBar.profile .profile img.show {
  display: block; }

